/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
  clear: both;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Oswald", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.width-1 {
  width: 10% !important;
}

.width-2 {
  width: 20% !important;
}

.width-3 {
  width: 30% !important;
}

.width-4 {
  width: 40% !important;
}

.width-5 {
  width: 50% !important;
}

.width-6 {
  width: 60% !important;
}

.textC {
  text-align: center;
}

.textL {
  text-align: left;
}

thead tr th {
  font-family: "Oswald", sans-serif;
}

.btns {
  text-shadow: none;
  font-size: 18px;
  text-decoration: none;
  border-radius: 3rem;
  color: black;
  background-color: transparent;
  padding: 1rem 3rem;
  border: 1px solid black;
  box-shadow: 2px 2px 3px 0px rgba(0, 0, 0, 0.2);
  transition: all 0.5s ease-out;
  margin: 1rem;
  display: inline-block;
  line-height: 1;
  font-weight: 700;
}
.btns:hover {
  color: white;
  background-color: #585858;
  box-shadow: 0px 0px 30px 15px white;
}

.btn-verde {
  color: #198754;
  border: 1px solid #198754;
}
.btn-verde:hover {
  background-color: #198754;
  box-shadow: 0 0 30px 15px #fff;
  border-color: black;
  color: #0c0c0c;
}

.btn-descartar {
  font-weight: 700;
  color: #dc3545;
  border: 1px solid #dc3545;
  background-color: transparent;
  line-height: 1;
  text-shadow: none;
  font-size: 18px;
}
.btn-descartar:hover {
  background-color: #dc3545;
  box-shadow: 0 0 30px 15px #fff;
  border-color: black;
  color: #0c0c0c;
}

.btn-cerrarS {
  color: white;
  height: 5rem;
  border-radius: 0;
  margin: 0;
  margin-bottom: 5rem !important;
  width: 100%;
  background-color: brown;
  text-align: center;
  line-height: 1.5;
}
.btn-cerrarS:hover {
  color: black;
  background-color: lightcoral;
  box-shadow: 0px 0px 3px 1px white;
}

.btn-eliminar {
  color: red;
  border-color: red;
  font-size: 14px;
  padding: 0.5rem 1rem;
  box-shadow: none;
}
.btn-eliminar:hover {
  background-color: red;
  box-shadow: 0 0 10px 1px grey;
}

.btn-actualizar {
  color: #f75408;
  border-color: #f75408;
  font-size: 14px;
  padding: 0.5rem 1rem;
  box-shadow: none;
}
.btn-actualizar:hover {
  background-color: #f75408;
  box-shadow: 0 0 10px 1px grey;
}

.linea-butons {
  display: flex;
}

.linea-butons-center {
  display: flex;
  justify-content: center;
}

.header {
  background: #da0000;
  background: -moz-linear-gradient(180deg, #da0000 0%, red 100%);
  background: -webkit-linear-gradient(180deg, #da0000 0%, red 100%);
  background: linear-gradient(180deg, #da0000 0%, red 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#da0000",endColorstr="#ff0000",GradientType=1);
  display: flex;
}
.header img {
  height: 8rem;
  width: 8rem;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 4rem;
}
.header img:hover {
  background-color: rgba(128, 128, 128, 0.5);
  cursor: pointer;
}
.header #boton_menu {
  position: fixed;
  right: 0;
  height: 5rem;
  width: 5rem;
  margin: 2rem;
  border-radius: 1rem;
}
.header #boton_menu:hover {
  background-color: gray;
  cursor: pointer;
}
.header h1 {
  margin: auto;
}

.volver {
  width: 40px;
  height: 40px;
  border: 1px solid black;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, white), color-stop(100%, #dcdcdc));
  border-radius: 50%;
  float: right;
  margin: 15px 25px;
  cursor: pointer;
  box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.2);
  transition: all 0.5s ease-out;
}
.volver:hover {
  padding: 0.5rem;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #d6d6d6), color-stop(100%, #b4b4b4));
  box-shadow: 0px 0px 30px 15px rgba(255, 255, 255, 0.5);
}

.footer {
  position: fixed;
  bottom: 0;
  background: #da0000;
  background: -moz-linear-gradient(0deg, #da0000 0%, red 100%);
  background: -webkit-linear-gradient(0deg, #da0000 0%, red 100%);
  background: linear-gradient(0deg, #da0000 0%, red 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#da0000",endColorstr="#ff0000",GradientType=1);
  width: 100%;
  height: 5rem;
}

.navegador {
  position: fixed;
  right: 0;
  top: 0;
  width: 0;
  height: 100vh;
  z-index: 1;
  background: #da0000;
  background: -moz-linear-gradient(0deg, #da0000 0%, #f30000 5%, red 90%, #da0000 100%);
  background: -webkit-linear-gradient(0deg, #da0000 0%, #f30000 5%, red 90%, #da0000 100%);
  background: linear-gradient(0deg, #da0000 0%, #f30000 5%, red 90%, #da0000 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#da0000",endColorstr="#da0000",GradientType=1);
  transition: all 0.5s ease;
  overflow: hidden;
}
.navegador .nav_list {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
  margin-top: 90px;
  padding: 0;
  visibility: hidden;
  opacity: 0;
}
.navegador .nav_list li {
  list-style-type: none;
  width: 100%;
  margin: 0;
}
.navegador .nav_list li a {
  display: block;
  color: white;
  padding: 1rem;
  text-shadow: 1px 1px black;
}
.navegador .nav_list li:hover {
  background: rebeccapurple;
}
.navegador.show {
  display: block;
  width: 15%;
  min-width: 15rem;
}
.navegador.show .nav_list {
  visibility: visible;
  opacity: 1;
}

#boton_menu {
  z-index: 100;
}

.main {
  width: 100%;
  left: 0;
  transition: all 0.5s ease;
}
.main.show {
  width: calc(100% - 15rem);
}
@media (min-width: 1024px) {
  .main.show {
    width: 85%;
  }
}

.logout {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  align-items: flex-end;
  left: 0;
  min-width: 15rem;
  width: 100%;
  z-index: -1;
}

table {
  background-color: lightsteelblue;
  border: 1px solid gray;
  border-bottom: 1px solid gray !important;
  border-radius: 5px;
  vertical-align: middle;
  width: 100%;
  border-spacing: 0;
  box-sizing: content-box;
}
table thead tr th {
  text-align: center;
  font-size: 16px;
  padding: 5px 10px;
  border-bottom: 2px solid #acbad5;
}
table tbody td {
  padding: 5px 10px;
  border-bottom: 2px solid #acbad5;
}
table tbody tr:nth-child(even) {
  background-color: #eee;
}
table tbody tr:nth-child(odd) {
  background-color: #fff;
}
table tbody tr.a_eliminar {
  background-color: gray;
  color: lightgray;
}

.dataTables_length {
  float: left;
  margin-bottom: 15px;
}
.dataTables_length label {
  font-size: 14px;
}
.dataTables_length label select {
  text-transform: none;
  border-radius: 20px;
  text-align: center;
  padding: 2px;
}

.dataTables_wrapper {
  clear: both;
}
.dataTables_wrapper:after {
  visibility: hidden;
  display: block;
  content: "";
  clear: both;
  height: 0;
}

.dataTables_filter {
  float: right;
  margin-bottom: 15px;
}

.dataTables_paginate {
  margin-top: 15px;
  float: right;
  text-align: right;
  padding-top: 0.25em;
}
.dataTables_paginate .paginate_button {
  border-radius: 50% !important;
}
.dataTables_paginate .paginate_button.disable {
  cursor: default;
  color: #666 !important;
  border: 1px solid transparent;
  background: transparent;
  box-shadow: none;
}

.dataTables_info {
  margin-top: 15px;
  clear: both;
  float: left;
  padding-top: 0.755em;
}

.btn-remito {
  background: #00a2ff;
  color: white;
  font-size: 17px;
  font-weight: 700;
}
.btn-remito.hover {
  color: rebeccapurple;
}
.btn-remito.selected {
  pointer-events: none;
  background: blue;
}

.contenido-remito {
  background-color: rgba(128, 128, 128, 0.1);
  padding: 2rem;
  border: 2px solid black;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  z-index: 1;
}
.contenido-remito .content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.contenido-remito .content p {
  margin: 0;
}
.contenido-remito .content p:not(:first-child) {
  margin-left: 10rem;
}
.contenido-remito .content:not(:last-child)::after {
  content: " ";
  background-color: rgba(128, 128, 128, 0.05);
  width: 100%;
  height: 0.5rem;
  border-radius: 0.5rem;
  display: block;
  margin: 2rem;
}

.oculto {
  display: none;
}

.hidden {
  display: none;
}